import React, { Component } from 'react'
import jeka from './gj.webp';
import './App.css';

class App extends Component {

    state = {
        date: new Date('2020-11-19T00:00:00'),
        dateShow: ""
    }

    componentDidMount() {
        requestAnimationFrame(this.getNewDate)
    }

    getNewDate = () => {
        this.setState({ dateShow: this.getTimerString(Math.abs( new Date() - this.state.date)) })
        setTimeout(() => requestAnimationFrame(this.getNewDate), 100);
    }

    getTimerString = (milSec) => {
        const days = Math.floor(milSec / 86400000);
        if (days === 0) {
            return new Date(new Date(milSec)).toISOString().slice(11, -1);
        } else {
            var dayText = " дней "
            if (days === 1) {
                dayText = " день "
            } else if (days === 2 || days === 3) {
                dayText = " дня "
            }
            return days+dayText+new Date(new Date(milSec)).toISOString().slice(11, -1);
        }
    }


  render() {

    return (
        <div className="App">
          <header className="App-header">
              <span className="textSpan" title={"тот самый"}>Это Евгений</span>
              <img src={jeka} className="photo" title={"Жека не алкаш"} alt="Jeka Gorodiskiy" />
              <span className="textSpan" title={"невероятно, но факт!"}>и он не пьет алкоголь уже аж</span>
              <span className="timerSpan" title={"не бухает с "+this.state.date.getFullYear()+"/"+(1+this.state.date.getMonth())+"/"+this.state.date.getDate()}>{this.state.dateShow}</span>
              <span className="textSpan" title={"да пребудет с ним сила!"}>пожелаем ему удачи :)</span>
          </header>
        </div>
    );
  }
}

export default App;
